@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0px;padding:0px}*:before,*:after{box-sizing:border-box}.main-container{background-image:url(/WeatherApp/static/media/background.9830b313.jpg);font-family:"Roboto", sans-serif;overflow:hidden;height:100vh}.search{display:flex;justify-content:center;align-items:center}.search input{margin-top:36px;width:300px;height:28px;border:none;background-color:white;padding-left:10px;border-bottom:2px solid steelblue}.search button{border:none;height:28px;width:60px;margin-top:36px;color:black;background-color:steelblue}.Info{display:flex;justify-content:center;align-items:center}.Info .Info-container{margin-top:20px;border-radius:20px;background:linear-gradient(blue, black);height:70vh;width:45vw}.Info .icon{display:flex;justify-content:center;align-items:center;padding-top:10px;width:100%;height:140px;color:white;border-radius:14px;background-color:linear-gradient(blue, black)}.Info i{font-size:80px}.weather-info{display:flex;font-size:20px;font-weight:400;margin-top:10px;height:120px;background-color:black}.weather-info .temperature{display:flex;justify-content:center;align-items:center;background-color:black;color:white;width:50%}.weather-info .temperature span{font-size:40px}.weather-info .discription{padding-top:30px;padding-left:30px;background-color:black;color:white;width:50%}.weather-info .discription .weather-condition{font-size:22px}.weather-info .discription .place{font-size:14px}.extra-Info{display:flex}.extra-Info .minmaxInfo{display:flex}.extra-Info .minmaxInfo .two-side-section{display:flex;margin-top:30px}.extra-Info .minmaxInfo .two-side-section p{margin-left:20px;color:skyblue}.extra-Info .minmaxInfo .two-side-section p i{font-size:30px;color:#00b3b3}

