@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");


* {
  margin: 0px;
  padding: 0px;
}
*:before,
*:after {
  box-sizing: border-box;
}
.main-container {
  background-image: url("./images/background.jpg");
  font-family: "Roboto", sans-serif;
  overflow: hidden;
  height: 100vh;
}
.search {
  display: flex;
  justify-content: center;
  align-items: center;
  input {
    margin-top: 36px;
    width: 300px;
    height: 28px;
    border: none;
    background-color: white;
    padding-left: 10px;
    border-bottom: 2px solid steelblue;
  }
  button {
    border: none;
    height: 28px;
    width: 60px;
    margin-top: 36px;
    color: black;
    background-color: steelblue;
  }
}
.Info {
  display: flex;
  justify-content: center;
  align-items: center;
  .Info-container {
    margin-top: 20px;
    border-radius: 20px;
    background: linear-gradient(blue, black);;
    height: 70vh;
    width: 45vw;
  }
  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
    width: 100%;
    height: 140px;
    color: white;
    border-radius: 14px;
    background-color: linear-gradient(blue, black);;
  }
  i {
    font-size: 80px;
  }
}
.weather-info {
  display: flex;
  font-size: 20px;
  font-weight: 400;
  margin-top: 10px;
  height: 120px;
  background-color: black;
  .temperature {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;
    width: 50%;
    span{
        font-size: 40px;
    }
  }
  .discription {
    padding-top: 30px;
    padding-left: 30px;
    background-color: black;
    color: white;
    width: 50%;
    .weather-condition{
        font-size: 22px;
    }
    .place{
       font-size: 14px;
    }
  }
  // .date {
  //   display: flex;
  //   justify-content: center;
  //   align-items: center;
  //   color: white;
  //   width: 20%;
  //   font-weight: 1000;
  //   padding-left: 10px;
  // }
}
.extra-Info {
  display: flex;
  .minmaxInfo 
    {
    display: flex;
    .two-side-section 
      {
      display: flex;
      margin-top: 30px;
      p 
       {
        margin-left: 20px;
        color: skyblue;
        i 
        {
          font-size: 30px;
          color: #00b3b3;
        }
      }
    }
  }
}
